.ui-slider-handle { position: absolute; top: 50%; margin: -10px 0 0 -10px; width: 20px; height: 20px; z-index: 2; border: 1px solid rgba(0, 0, 0, 0.1); -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; border-radius: 100%; background-color: #fff; box-shadow: 0 2px 1px rgba(0, 0, 1, 0.05); transition: 0.3s; }
